.content{display:flex;align-items:center}.ParliamBlock{width:249px;height:324px;padding:20px;transition:transform .3s ease,box-shadow .3s ease;box-shadow:10px 0 15px rgba(0,0,0,.3);border-radius:10px;text-decoration:none;background:linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.7) 80%),url(../../../../../../assets/images/students_parlament.svg);background-size:cover;background-position:center}.ParliamBlock:hover{box-shadow:10px 0 15px rgba(0,0,0,.3);transform:translateY(-10px)}.ParliamBlock_text{display:flex;font-size:26px;color:#fff;height:100%;text-decoration:none;text-align:left}@media(max-width: 1100px){.ParliamBlock{width:276px;height:164px}.ParliamBlock_text{font-size:16px;color:#fff;text-decoration:none;align-items:flex-end;justify-content:center}}@media(max-width: 615px){.ParliamBlock{width:206px;height:114px}.ParliamBlock_text{font-size:14px}}@media(max-width: 474px){.ParliamBlock:hover{transform:translateY(-5px)}.ParliamBlock{width:186px;height:104px}.ParliamBlock_text{font-size:12px}}@media(max-width: 430px){.ParliamBlock{width:150px;height:94px}.ParliamBlock_text{font-size:9px}}